body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --main-color: rgba(175, 200, 225, 1);
    --main-color: white;
    --dark-color: rgba(100, 100, 125, 1);
}
html {
    background-color: white;
    background-color: var(--main-color);
}
p, h5 {
    margin: 0;
    padding: 0;
}
a {
    color: black;
}
.issue-header {
    font-weight: 400;
    font-size: 9pt;
}
.issue a {
    font-size: 9pt;
    color: salmon;
}
.issue-body a {
    color: salmon;
    font-size: 10pt;
}
.issue-group-item {
    width: 400px;
    margin: 1em;
}
.issue-body a:hover, .issue a:hover {
    color: rgba(100, 100, 125, 1);
    color: var(--dark-color);
    text-decoration: none;
}
.content-title {
    z-index: 9998;
    background-color: white;
    background-color: var(--main-color);
}
.content-title h5 {
    margin: 0;
    padding: 0;
}
.logo {
    width: 50px;
    margin-right: 1rem;
}
.header {
    /* background-color: rgba(175, 200, 225, 1); */
    background-color: rgba(100, 100, 125, 1);
    background-color: var(--dark-color);
    width: 100%;
    color: white;
    height: 60px;
    z-index: 9999;
}
.header .title {
    font-weight: 250;
}
.header p {
    margin: 0;
    padding: 0;
    font-size: 10pt;
}
.main {
    height: 100%;
    background-color: white;
    background-color: var(--main-color);
}
.issues-container {
    /* padding-top:100px; */
    z-index: 0;
}
.issue {
    width: 100%;
    padding: 1rem;
    background-color: white;
    border: 1px solid rgba(100, 100, 125, 1);
    border: 1px solid var(--dark-color);
    border-radius: 5px;
}
.issue-body {
    font-size: 10pt;
    text-align: left;
}
.user {
    /* border: 1px solid var(--dark-color); */
    /* border-radius: 5px; */
    font-size: 9pt;
}
.user-avatar {
    width: 75px;
    height: 75px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

